import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';

export default class Badge extends Component {
  state={
    redirect: false
  }

  redirectState(){
    this.setState({redirect: true})
  }

  redirect(){
    if(this.state.redirect){
      if(this.props.link){
        return <Redirect to={"/BecodeApp/badge/" + this.props.badge.entityId}/>
      }
    }
  }

  render() {
    return (
      <div className="card">
        <section>
          <a onClick={this.redirectState.bind(this)}>
          <div className="pathHit__inner">
            <span className="pathHit__categoryLabel">
              Badge
            </span>
            <div className="pathHit__information"> 
              <i className="pathHit__informationIcon">
                  <img src={this.props.badge.image} alt="badge" />  
              </i>
              <span className="pathHit__informationLabel">
                {this.props.badge.name}
              </span>
            </div>
            <p className="pathHit__description">
              {this.props.badge.description}
            </p>
          </div>
          </a>
          <a href={this.props.badge.openBadgeId} target="_blank">
            <div className="pathHit__action">
              <span className="pathHit__actionLabel">
                  <span className="pathAction pathAction--open">
                    Voir le parcours
                  </span>
              </span>
            </div>
          </a>
        </section>
        {this.redirect()}
      </div>
    );
  }
}



