import React, { Component } from 'react';
import ReactDOM from 'react-dom';
//CSS
import './Assets/style.css';
// Component
import App from './App';
import Home from './Home';
import Send from './Send';
import BadgeClass from './Component/BadgeClass';
import NotFound from './NotFound'
// Router
import { BrowserRouter as Router, Route, Link, Switch } from 'react-router-dom';

import registerServiceWorker from './registerServiceWorker';

export default class Root extends Component{
    render(){
        return (
            <Router>
                <div>
                    <ul className="menu">
                        <li><Link to="/BecodeApp/">Home</Link></li>
                        <li><Link to="/BecodeApp/badge">Badge Subscription</Link></li>
                        <li className="slider"></li>
                    </ul>
                    <Switch>
                        <Route exact path='/BecodeApp/' component={ Home } />
                        <Route exact path='/BecodeApp/badge/:id' component={ App } />
                        <Route exact path='/BecodeApp/badge/:id/sending' component={ Send } />
                        <Route exact path='/BecodeApp/badge' component={ BadgeClass } />
                        <Route component={ NotFound } />
                    </Switch>
                </div>
            </Router>
        )
    }
}

ReactDOM.render(<Root />, document.getElementById('root'));
registerServiceWorker();



