import React, { Component } from 'react';
import Rebase from 're-base';
import app from './Base';
import axios from 'axios';
import Badge from './Component/Cards/Badge';
import { Redirect } from 'react-router-dom';

var base = Rebase.createClass(app.database());

export default class App extends Component {
    state={
        badges: [],
        input: {},
        response: false,
        type: [],
        error: [],
        inputLenght: 0,
        alert: ""
    }

    componentDidMount(){
      base.syncState(`/`, {
        context: this,
        state: 'badges',
        asArray: true
      });
      let config = {
        headers: {'Authorization': 'Token 87094e6352cd1f8b4e3b5e79f6bb1fdb6c888b83'}
      };
      let these = this;
      axios.get("https://api.badgr.io/v2/badgeclasses/"+this.props.match.params.id, config)
      .then(function (response) {
          these.setState({input: response.data.result[0]});
          these.setState({response: true});
          these.setState({inputLenght: response.data.result.length});
      })
      .catch(function (errors) {
          let error = 'error';
          these.setState({response: false});
          these.setState({type: error});
          these.setState({error: errors});
      }); 
    }

    formInput(e){
      e.preventDefault();
      this.setState({alert: false})
      let mail = document.getElementById('mail').value;
      let repo = document.getElementById('repo').value;
      let url = document.getElementById('url').value;
      var testInput = this.testUrl(repo, url);
      var mailInput = this.checkEmail(mail);
      if(testInput && mailInput && this.state.input.entityId){
        var badges = {
        mail: mail, 
        repo: repo, 
        url: url, 
          badge: this.state.input.entityId
        }
        var listBadges = this.state.badges;
        listBadges.push(badges);
        this.setState({badges: listBadges});
        this.setState({alertInput: ""});
        this.setState({redirect: true});
        //document.location.href=this.state.input.entityId+"/sending"
      }else{
        this.setState({alert: "Veullez remplir tout les champs correctement et avec les liens complet en HTTPS"});
      }
    }

    testUrl(repo, url){
      let repoTesting = repo.substr(0, 19) ===  "https://github.com/" ? true : false; // test si les premiers caratère sont bien https://github.com/
      //let urlTesting = url.substr(0, 8) ===  "https://" ? true : false; // test si les premiers caratère sont bien https:// ou http://
      if(repoTesting){
        return true;
      }
    }

    checkEmail(email) {
      var re = /^(?:[a-z0-9!#$%&amp;'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&amp;'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])$/;
      return re.test(email);
    }

    error(){
      if(this.state.alert){
        var iDiv = document.createElement('div');
        iDiv.className = 'alert';
        iDiv.innerText = this.state.alert;
        document.getElementsByTagName('body')[0].appendChild(iDiv);
      }
     }

    charging(){
      if(this.state.redirect){
        return <Redirect to={this.state.input.entityId+"/sending"}/>
      } else if(this.state.inputLenght > 0){
        return (
          <div>
            <div className="pen-title"> </div>
            <div className="module form-module">
              <div className=""></div>
              <div className="form">
                <h2>Completer pour proposer un badge</h2>
                <form onSubmit={this.formInput.bind(this)}>
                  <input id="mail" type="email" name="mail" placeholder="Mail"/>
                  <input id="repo" type="text" name="repo" placeholder="Repo Githup"/>
                  <h6>Lien du repo GitHub dan le quel se trouve les fichiers correspondant</h6>
                  <input id="url" type="text" name="url" placeholder="Url du projet"/>
                  <h6>Le lien du site qui nous permettra de tester le tout (optionnel)</h6>
                  <input type="submit" value="send" />
                  <h6>
                    Pour rappel, tout les badges ne peuvent être passer par cette méthode, restez logique.<br/>
                    Tout abus est sanctionnable 
                  </h6>
                </form>
              </div>
            </div>
            <div className="cards">
              <Badge 
                badge={this.state.input} 
                link={false}
              />
            </div>
          </div>
        )
      }else if(this.state.response){
        return (
          <div id="preloader">
            <div id="loader"></div>
            <div className="errorloading">
              Le badge selectioner est incorrecte
            </div>
          </div>
        );
      }else{
        return(
          <div className="errorstateloading">
            {this.state.error.message}
          </div>
        )
      }
    }

    render(){
      return(  
        <div>
          {this.charging()}
          {this.error()}
        </div>
      )
    }
}



