import React, { Component } from 'react';
import axios from 'axios';
import Badge from './Cards/Badge';

export default class BadgeClass extends Component{
    constructor(props) {
        super(props);
        this.state = {
          demand:{
            issuers : ['https://api.badgr.io/v2/issuers', 'issuers', 'response.data.result[0]'], // return object issuers
            collections : ['https://api.badgr.io/v2/badgeclasses', 'badgesClasses', 'response.data.result'] // table with badges
          },
          input: {},
          error: [],
          type: [],
          searchString: '',
          response: false
        };
      }
      
      componentDidMount(){
        let config = {
          headers: {'Authorization': 'Token 87094e6352cd1f8b4e3b5e79f6bb1fdb6c888b83'}
        };
        let these = this;
        axios.get(this.state.demand.collections[0], config)
        .then(function (response) {
            these.setState({input: response.data.result});
            these.setState({response: true});
        })
        .catch(function (errors) {
            let error = 'error';
            these.setState({response: false});
            these.setState({type: error});
            these.setState({error: errors});
        }); 
      }
    
      handleChange(event){
        var re = /\W/;
        var re2 = /\s/;
        if(!re.test(event.target.value)){
          this.setState({searchString: event.target.value});
        }
        if(re2.test(event.target.value)){
          this.setState({searchString: event.target.value});
        }
      }
      
      charging(libraries){
        var table = libraries;
        if(table[0]){
          return (
            table.map((badge,index) => {
              return (
                <Badge 
                  badge={badge} 
                  index={index} 
                  key={index}
                  link={true}
                />
              )
            })
          )
        }else if(this.state.response){
          return (
            <div id="preloader">
              <div id="loader"></div>
              <div className="errorloading">
                Votre recherche n'a pas abouti
              </div>
            </div>
          );
        }else{
          return(
            <div id="preloader">
              <div id="loaderinfini"></div>
              <div className="errorloading">
                Réseau lent...
              </div>
            </div>
          )
        }
      }
  
      rendu(){
        if(this.state.type !== 'error'){
          var libraries = this.state.input;
          var searchString = this.state.searchString.trim().toLowerCase();
          if(searchString.length > 0){
            libraries = libraries.filter(function(l){
                return l.name.toLowerCase().match( searchString );
            });
          }
          return(
            this.charging(libraries)
          ) 
        }else{
          return(
            <div className="errorstateloading">
              {this.state.error.message}
            </div>
          )
        }
      }
  
      render() {
        return (
          <div className="cards">
            <h6>
              Sélectionner le badge à valider<br/>
            </h6>
            <div className="searchBar">
              <div className="wrap">
                <div className="search">
                  <input 
                    type="text" 
                    value={this.state.searchString} 
                    onChange={this.handleChange.bind(this)} 
                    className="searchTerm" 
                    placeholder="Rechercher" 
                  />
                </div>
              </div>
            </div>
            {this.rendu()}
          </div>
        );
      }
}


