import React, { Component } from 'react';
import axios from 'axios';
import Badge from './Component/Cards/Badge';
import { Redirect } from 'react-router-dom';

export default class Send extends Component {
    state={
        badges: [],
        input: {},
        response: false,
        type: [],
        error: [],
        inputLenght: 0,
        alert: "",
        redirect: false
    }

    componentDidMount(){
      let config = {
        headers: {'Authorization': 'Token 87094e6352cd1f8b4e3b5e79f6bb1fdb6c888b83'}
      };
      let these = this;
      axios.get("https://api.badgr.io/v2/badgeclasses/"+this.props.match.params.id, config)
      .then(function (response) {
          these.setState({input: response.data.result[0]});
          these.setState({response: true});
          these.setState({inputLenght: response.data.result.length});
      })
      .catch(function (errors) {
          let error = 'error';
          these.setState({response: false});
          these.setState({type: error});
          these.setState({error: errors});
      }); 
    }

    redirectState(){
      this.setState({redirect: true})
    }

    charging(){
      console.log(this.props.match.params.id)
      if(this.state.redirect){
        return <Redirect to={"/BecodeApp"}/>
      }else if(this.state.inputLenght > 0){
        return (
          <div>
            <div className="send-title">
              Votre demande a bien été envoyée !
            </div>
            <div className="container">
              <a onClick={this.redirectState.bind(this)}><button id="button"></button></a>
            </div>
            <div className="cards">
              <Badge 
                badge={this.state.input} 
                link={true}
              />
            </div>
          </div>
        )
      }else if(this.state.response){
        return (
          <div id="preloader">
            <div id="loader"></div>
            <div className="errorloading">
              Le badge selectioner est incorrecte
            </div>
          </div>
        );
      }else{
        return(
          <div className="errorstateloading">
            {this.state.error.message}
          </div>
        )
      }
    }

    render(){
      return(  
        <div>
          {this.charging()}
        </div>
      )
    }
}



